<template>
  <div class="register-contain">

    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm" >

      <el-form-item  prop="username">
        <el-input placeholder="请输入用户名" class="register-input" v-model.number="ruleForm.username"></el-input>
      </el-form-item>

      <el-form-item prop="pass">
        <el-input placeholder="请输入密码" class="register-input" type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item prop="checkPass">
        <el-input type="password" placeholder="确认密码" class="register-input" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>

      <el-button type="primary" plain class="register-button-left" @click="submitForm('ruleForm')">提交</el-button>
      <el-button type="primary" plain class="register-button-right" @click="resetForm('ruleForm')">重置</el-button>

    </el-form>

  </div>
</template>

<script>
  export default {
    name: "Register",
    data() {
      var checkUsername = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('用户名不能为空'));
        }
        else {
          return callback()
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          pass: '',
          checkPass: '',
          username: ''
        },
        rules: {
          pass: [
            {validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {validator: validatePass2, trigger: 'blur'}
          ],
          username: [
            {validator: checkUsername, trigger: 'blur'}
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios.post('/blog/api/register',{
              username: this.ruleForm.username,
              password: this.ruleForm.pass
            }).then((res) => {
              if(res.data == true){
                this.$message({
                  showClose: true,
                  message: '注册成功',
                  type: 'success'
                })
              }else{
                this.$message({
                  showClose: true,
                  message: '注册失败',
                  type: 'error'
                })
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }

  }
</script>

<style scoped>
  .register-contain {
    width: 40%;
    margin: 100px auto;
    border: 1px solid #DCDFE6;
    box-shadow: 0 0 8px rgba(0, 0, 0, .4);
    padding: 20px;
    overflow: hidden;
  }

  .register-input {
    margin-bottom: 20px;
    width: 100%;
  }

  .register-button-left {
    width: 45%;
    float: left;
  }

  .register-button-right {
    width: 45%;
    float: right;

  }

</style>
